import React, { createContext, useReducer, useEffect } from "react";
import Reducer from "./Reducer";

const initialState = {
  incomeTransaction:
    JSON.parse(localStorage.getItem("incomeTransaction")) || [],
  expenseTransaction:
    JSON.parse(localStorage.getItem("expenseTransaction")) || [],
};

export const GlobalContext = createContext(initialState);

export const GlobalContextProvider = ({ children }) => {
  const [state, dispatch] = useReducer(Reducer, initialState);

  useEffect(() => {
    localStorage.setItem(
      "incomeTransaction",
      JSON.stringify(state.incomeTransaction)
    );
    localStorage.setItem(
      "expenseTransaction",
      JSON.stringify(state.expenseTransaction)
    );
  });

  return (
    <GlobalContext.Provider
      value={{
        incomeTransaction: state.incomeTransaction,
        expenseTransaction: state.expenseTransaction,
        dispatch,
      }}>
      {children}
    </GlobalContext.Provider>
  );
};
