import React, { useState, useContext } from "react";
import { v4 as uuidv4 } from "uuid";
import { GlobalContext } from "./../context/globalState";

const Transaction = () => {
  const { dispatch } = useContext(GlobalContext);
  const [income, setIncome] = useState({
    incomeText: "",
    incomeAmount: 0,
  });
  const [expense, setExpense] = useState({
    expenseText: "",
    expenseAmount: 0,
  });

  const { incomeText, incomeAmount } = income;
  const { expenseText, expenseAmount } = expense;

  const onChangeIncome = e => {
    setIncome({ ...income, [e.target.name]: e.target.value });
  };

  const onChangeExpense = e => {
    setExpense({ ...expense, [e.target.name]: e.target.value });
  };

  const onSubmitIncome = e => {
    e.preventDefault();

    if (income.incomeText !== "") {
      const newIncome = {
        id: uuidv4(),
        incomeText,
        incomeAmount: incomeAmount * 1, //converting to number
      };

      dispatch({ type: "add__income", payload: newIncome });
      setIncome({ incomeText: "", incomeAmount: 0 });
    }
  };

  const onSubmitExpense = e => {
    e.preventDefault();

    if (expense.expenseText !== "") {
      const newExpense = {
        id: uuidv4(),
        expenseText,
        expenseAmount: expenseAmount * 1, //converting to number
      };

      dispatch({ type: "add__expense", payload: newExpense });
      setExpense({ expenseText: "", expenseAmount: 0 });
    }
  };

  return (
    <div className="form-wrapper">
      <form onSubmit={onSubmitIncome}>
        <div className="input-group income">
          <input
            onChange={onChangeIncome}
            type="text"
            name="incomeText"
            value={incomeText}
            placeholder="Add Income..."
            autoComplete="off"
          />
          <input
            onChange={onChangeIncome}
            type="number"
            name="incomeAmount"
            value={incomeAmount}
            placeholder="Amount"
            autoComplete="off"
          />
          <input type="submit" value="Submit" />
        </div>
      </form>
      <form onSubmit={onSubmitExpense}>
        <div className="input-group expense">
          <input
            name="expenseText"
            onChange={onChangeExpense}
            type="text"
            value={expenseText}
            placeholder="Add Expense..."
            autoComplete="off"
          />
          <input
            name="expenseAmount"
            onChange={onChangeExpense}
            type="number"
            value={expenseAmount}
            placeholder="Amount"
            autoComplete="off"
          />
          <input type="submit" value="Submit" />
        </div>
      </form>
    </div>
  );
};

export default Transaction;
