export default (state, action) => {
  switch (action.type) {
    case "add__income":
      return {
        ...state,
        incomeTransaction: [action.payload, ...state.incomeTransaction],
      };

    case "add__expense":
      return {
        ...state,
        expenseTransaction: [action.payload, ...state.expenseTransaction],
      };

    case "delete__income": {
      const incomeId = action.payload;
      const incomeList = [...state.incomeTransaction];
      return {
        ...state,
        incomeTransaction: incomeList.filter(x => x.id !== incomeId),
      };
    }

    case "delete__expense": {
      const expenseId = action.payload;
      const expenseList = [...state.expenseTransaction];
      return {
        ...state,
        expenseTransaction: expenseList.filter(x => x.id !== expenseId),
      };
    }

    default:
      return state;
  }
};
