import React, { useContext } from "react";
import { MdDelete } from "react-icons/md";
import { GlobalContext } from "./../context/globalState";

const IncomeTransaction = ({ transaction }) => {
  const { dispatch } = useContext(GlobalContext);

  const deleteIncome = () => {
    dispatch({ type: "delete__income", payload: transaction.id });
  };

  return (
    <div>
      <li className="transaction">
        <span className="transaction-text">{transaction.incomeText}</span>
        <span className="transaction-amount">${transaction.incomeAmount}</span>
        <button onClick={deleteIncome} className="delete-btn">
          <MdDelete />
        </button>
      </li>
    </div>
  );
};

export default IncomeTransaction;
