import React, { useContext } from "react";
import { GlobalContext } from "./../context/globalState";

const Balance = () => {
  const { incomeTransaction, expenseTransaction } = useContext(GlobalContext);

  const totalIncome = incomeTransaction
    .reduce((total, value) => total + value.incomeAmount, 0)
    .toFixed(2);

  const totalExpense = expenseTransaction
    .reduce((total, value) => total + value.expenseAmount, 0)
    .toFixed(2);

  const Balance = () => {
    return totalIncome - totalExpense;
  };

  return (
    <div className="balance">
      <h2>Your Balance</h2>
      <h3>${Balance().toFixed(2)}</h3>
      <div className="income-expense">
        <div className="plus">
          <h3>Income</h3>
          <p>+${totalIncome}</p>
        </div>
        <div className="minus">
          <h3>Expenses</h3>
          <p>-${totalExpense}</p>
        </div>
      </div>
    </div>
  );
};

export default Balance;
