import React, { useContext } from "react";
import { MdDelete } from "react-icons/md";
import { GlobalContext } from "./../context/globalState";

const ExpenseTransaction = ({ transaction }) => {
  const { dispatch } = useContext(GlobalContext);

  const deleteExpense = () => {
    dispatch({ type: "delete__expense", payload: transaction.id });
  };

  return (
    <div>
      <li className="transaction">
        <span className="transaction-text">{transaction.expenseText}</span>
        <span className="transaction-amount">${transaction.expenseAmount}</span>
        <button onClick={deleteExpense} className="delete-btn">
          <MdDelete />
        </button>
      </li>
    </div>
  );
};

export default ExpenseTransaction;
